<template>
<!--    <el-card class="box-card">-->
<!--        <div slot="header" class="clearfix">-->
<!--            修改合同-->
<!--        </div>-->

        <div>
            <el-form ref="auditForm" :model="auditForm" :rules="rules" label-position="left" label-width="100px">
                <el-form-item label="审批意见" prop="info">
                    <el-input type="textarea" :autosize="{minRows:5, maxRows:10} " v-model="auditForm.info" ></el-input>
                </el-form-item>

                 <el-form-item>
                    <el-row>
                        <el-col :span="3">
                            <el-radio v-model="auditForm.radio" label="1">通过</el-radio>
                        </el-col>

                        <el-col :span="9">
                            <el-radio v-model="auditForm.radio" label="2">拒绝</el-radio>
                        </el-col>
                    </el-row>
                </el-form-item>


                <el-form-item>
                    <el-row>
                        <el-col :span="15">
                            <el-button type="primary" @click="postContract">确定</el-button>
                        </el-col>

                        <el-col :span="9">
                            <el-button type="danger" @click="resetContract">重置</el-button>
                        </el-col>
                    </el-row>
                </el-form-item>

            </el-form>
        </div>
<!--    </el-card>-->
</template>

<script>
    export default {
        name: "editaudit",
        data() {
            return {
                auditForm: {
                    info: '',
                    
                },
                nullContract: {
                    info: '',
                   
                },
                rules: {
                    info: [
                        {required: true, message: '请输入审批意见', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            resetContract() {
                this.$confirm("此操作将清空该审批意见，是否继续？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    this.auditForm = this.cleanJson(this.auditForm);
                    this.$notify({
                        title: '成功',
                        message: '清空成功！',
                        type: "success"
                    })
                })
            },
            postContract() {
                this.$refs["auditForm"].validate((valid) => {
                    if (valid) {
                        
                        this.$axios({
                            url:"/contract/audit",
                            method: 'post',
                            data: {
                                info: this.auditForm.info,
                                radio: this.auditForm.radio
                            },
                            transformRequest: [function (data) {
                                let ret = '';
                                for (let it in data) {
                                    ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                                }
                                return ret
                            }],
                            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
                        }).then(res => {
                            // eslint-disable-next-line no-console
                            console.log(res);
                        })
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '提交失败！',
                            type: "error"
                        })
                    }
                })
            },
            cleanJson(json) {
                for (let draftFormKey in json) {
                    json[draftFormKey] = ''
                }
                return json;
            }
        }
    }
</script>

<style scoped>
    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }
